<template>
  <div id="main">
    <div id="header">
      <HeaderComponent />
    </div>
    <div id="content">
      <div v-if="this.loading" class="d-flex align-items-center container">
        <strong>Loading...</strong>
        <div class="spinner-border ms-auto" role="status" aria-hidden="true"></div>
      </div>
      <div v-else class="sidebar-menu-container" id="sidebar-menu-container">
        <div class="container mt-5">
          <h3 style="text-align:center">ข้อมูลอาจารย์ผู้สอน</h3>
          <hr />
          <div class="row">
              <div class="">
                <img :src="Teacher.image" width="70%" alt="">
              </div>
            <div class="card" style="padding:10px 10px 10px 10px; margin-left:10px; width:18rem">
              <div class="" style="">
                <p style="text-decoration:underline; font-weight:t"> ชื่อ-นามสกุล :</p> {{ Teacher.name }} <br><br>
                <p style="text-decoration:underline"> วิชาที่สอน :</p> {{ Teacher.subjectName }} <br><br>
                <p style="text-decoration:underline"> อีเมล :</p> {{ Teacher.email }} <br><br>
                <p style="text-decoration:underline"> เบอร์โทรติดต่อ :</p> {{ Teacher.phone }} <br><br>
              </div>
            </div>
          </div>
        <a class="backbt mt-4" type="button" @click="$router.go(-1)" style="text-decoration:none; color:white"><i class="fas fa-chevron-left"></i> Back </a>
        </div>
      </div>
    </div>

    <div id="footer">
      <FooterComponent />
    </div>
  </div>
</template>

<script scope>
import axios from "axios";
import HeaderComponent from "../../components/HeaderComponent.vue";
import FooterComponent from "../../components/FooterComponent.vue";
export default {
  name: "teacher",
  components: { HeaderComponent, FooterComponent },
  data() {
    return {
      Teacher: [],
      loading: true,
    };
  },
  mounted() {
    this.DataTeacher();
  },
  methods: {
    DataTeacher(page) {
      axios
        .get("/api/teacher_id")
        .then((res) => {
          for (var i = 0; i < res.data.length; i++) {
            if (res.data[i].id == this.$route.params.id) {
              this.Teacher = {
                id: res.data[i].id,
                name: res.data[i].name,
                image: res.data[i].user_photo,
                subjectName: res.data[i].subjectName,
                email: res.data[i].email,
                phone: res.data[i].phone,
              };
            }
            this.loading = false;
          }
          //   console.log(this.subject_id);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
<style>
/* ##### >> https://fonts.google.com/specimen/Sarabun?query=sara#standard-styles */
@import url('https://fonts.googleapis.com/css2?family=Sarabun:wght@300&display=swap');

h1,h2,h3,h4,h5,h6 {
  font-family: 'Sarabun', sans-serif;
}
#content {
  margin-top: 5%;
}
.backbt{
  width: 80px;
  height: 40px;
  background: #a12c2f;
  border-radius: 2px;
  float: right;
  text-align: center;
  line-height: 40px;
  color: white;
  font-size: 16px;
}
.backbt:hover{
  background: linear-gradient(135deg, #a12c2f, #e43c42);
}
</style>
