ht;\r\n  text-align: center;\r\n  line-height: 40px;\r\n  color: white;\r\n  font-size: 16px;\r\n}\r\n.backbt:hover{\r\n  background: linear-gradient(135deg, #a12c2f, #e43c42);\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
